import { DateObject } from "./proxycurl_api";
import { CACHE_VERSION_ID } from './constant';
import { createHash } from 'crypto';

export enum SocialMediaType {
  Facebook = 'facebook',
  Twitter = 'twitter',
  LinkedIn = 'linkedin',
}
const EMPTY_VALUE = 'EMPTYSTRING';
export const SNSProfile = {
  [SocialMediaType.Facebook]: 'facebook_profile_url',
  [SocialMediaType.Twitter]: 'twitter_profile_url',
  [SocialMediaType.LinkedIn]: 'linkedin_profile_url',
};

// Maximum value for cache: 6 Hours
const  expirationInSeconds = 21600;

const LinkedinPersonURLregex = /^https?:\/\/([a-zA-Z]+\.)?linkedin\.com\/in\/\S+\/?/;
const emptyString = 'No data';

type ResultType = string | number | boolean | Date | unknown[];

export function combine_cache_key(...args: string[]): string {
  const combinedString = args.join(' ');
  return combinedString;
}

function isDate(obj: unknown): obj is DateObject {
  if (typeof obj !== 'object' || obj === null) {
    return false;
  }
  return 'day' in obj && 'month' in obj && 'year' in obj;
}

// Handle result so it complies with sheet
export function handleResult(value: unknown): ResultType {
  if (value === undefined || value === null || value === "") {
    // check if empty
    return emptyString;
  } else if (
    typeof value === "number" ||
    typeof value === "boolean"
  ) {
    // normal type
    return value;
  } else if (
    typeof value === "string"
  ) {
    // format the string to make it one line
    const newString = value.replace(/\n/g, '\\n');
    return newString;
  } else if (isDate(value)) {
    // if receive Date then return new Date
    return new Date(
      value.year,
      value.month - 1,
      value.day
    );
  } else if (Array.isArray(value)) {
    if (value.length == 0) {
      return emptyString;
    }

    if (isUrl(value[0] as string)) {
      const modifiedArray = value.map(url => encodeURI(url as string));
      // if array has url then return original array
      return modifiedArray as unknown[];
    } else {
      // if array doesn't contain url then make it csv
      return value.toString();
    }
  } else if (typeof value === "object") {
    if (Object.keys(value).length === 0) {
      return emptyString;
    }
    // this should not executed if correct flow
    return JSON.stringify(value);
  }
  // this should not executed if correct flow
  return emptyString;
}

export function check_all_params(
  allow_blank: boolean,
  required_params: string[],
  params: Record<string, unknown>
): void {
  for (const param of required_params) {
    const value = params[param];
    if (
      value === undefined ||
      value === null ||
      value === ''
    ) {
      throw new Error(`Required parameter '${param}' is missing.`);
    }
  }

  for (let arg of Object.values(params)) {
    if (typeof arg === 'string') {
      arg = arg.toLowerCase();
    }
    if (
      arg === undefined ||
      arg === null ||
      (!allow_blank && arg === '')
    ) {
      throw new Error("Arguments can't be blank/null.");
    } else if (
      arg === '#REF!'.toLowerCase()
    ) {
      throw new Error(`Arguments contain one or more REF errors.`);
    } else if (
      arg === '#NAME?'.toLowerCase() ||
      arg === '#VALUE!'.toLowerCase() ||
      arg === '#DIV/0!'.toLowerCase() ||
      arg === '#NULL!'.toLowerCase() ||
      arg === '#NUM!'.toLowerCase() ||
      arg === '#N/A'.toLowerCase() ||
      arg === '#ERROR!'.toLowerCase()
    ) {
      throw new Error(`Arguments contain one or more [${arg.toUpperCase()}] errors.`);
    } else if (
      typeof arg === "string" ||
      typeof arg === "number" ||
      typeof arg === "boolean"
    ) {
      continue;
    } else {
      throw new Error("Arguments not defined.");
    }
  }
}

export function handleBooleanParam(
  value: unknown, default_value: unknown = EMPTY_VALUE
): string {
  if ((value== undefined || value == '') && default_value != EMPTY_VALUE){
    value = default_value;
  }
  if (
    typeof value !== "boolean" &&
    typeof value !== "string"
  ) {
    throw new Error('One of the parameters only support boolean value');
  }
  if (typeof value === "boolean") {
    value = value ? 'true' : 'false';
  }
  return String(value).toLowerCase();
}

export function handleNumberParam(
  value: unknown, default_value: unknown = EMPTY_VALUE
): number {
  if ((value== undefined || value == '') && default_value != EMPTY_VALUE){
    value = default_value;
  }
  if (typeof value !== "number" || value < 0) {
    throw new Error('One of the parameters only support positive number value');
  }
  return value;
}

export function handleStringParam(
  value: unknown,
  default_value: unknown = EMPTY_VALUE,
  preserve_input_casing = false
): string {
  if ((value== undefined || value == '') && default_value != EMPTY_VALUE){
    value = default_value;
  }
  if (typeof value !== "string") {
    throw new Error('One of the parameters only support string value');
  }
  if (preserve_input_casing) {
    return value.trim();
  }
  return value.toLowerCase().trim();
}

export function isKeyOf<T extends object>(obj: T, attr: string): boolean {
  return Object.keys(obj).includes(attr);
}

export function validateSchoolLinkedinURL(url: string): void {
  const regex = /^https?:\/\/([a-zA-Z]+\.)?linkedin\.com\/school\/\S+\/?/;
  if (!regex.test(url)) {
    throw new Error('URLs should be in the format of https://linkedin.com/school/<public-identifier>');
  }
}

export function validateCompanyLinkedinURL(url: string): void {
  const regex = /^https?:\/\/([a-zA-Z]+\.)?linkedin\.com\/company\/\S+\/?/;
  if (!regex.test(url)) {
    throw new Error('URLs should be in the format of https://linkedin.com/company/<public-identifier>');
  }
}

export function validatePersonLinkedinURL(url: string): void {
  if (!LinkedinPersonURLregex.test(url)) {
    throw new Error('URLs should be in the format of https://linkedin.com/in/<public-identifier>');
  }
}

export function validateJobLinkedinURL(url: string): void {
  const regex = /^https?:\/\/([a-zA-Z]+\.)?linkedin\.com\/jobs\/view\/\S+\/?/;
  if (!regex.test(url)) {
    throw new Error('URLs should be in the format of https://linkedin.com/jobs/view/<public-identifier>');
  }
}

export function validateAndCheckSocialMediaURL(url: string): SocialMediaType {
  const TwitterPersonURLregex = /^https?:\/\/([a-zA-Z]+\.)?twitter\.com\/\S+\/?/;
  const FacebookPersonURLregex = /^https?:\/\/([a-zA-Z]+\.)?facebook\.com\/\S+\/?/;

  if(LinkedinPersonURLregex.test(url)){
    return SocialMediaType.LinkedIn;
  } else if(TwitterPersonURLregex.test(url)){
    return SocialMediaType.Twitter;
  } else if(FacebookPersonURLregex.test(url)){
    return SocialMediaType.Facebook;
  }
  throw new Error(`
    URLs should be in the format of
    https://linkedin.com/in/<public-identifier>
    or https://twitter.com/<public-identifier>
    or https://facebook.com/<public-identifier>
  `);
}

function GetMD5(input: string): string {
    return createHash('md5')
        .update(input)
        .digest('hex');
}
  

export function generateCellCacheKey(
  documentId: string,
  worksheetId: number,
  cellId: string,
  formulaText : string,
) {
  const stringCacheKey = `${CACHE_VERSION_ID}-${documentId}-${worksheetId}-${cellId}-${formulaText}`;
  const hashedKey = GetMD5(stringCacheKey);
  return hashedKey;
}

function isUrl(data: string) {
  if (typeof data === 'string') {
    return data.startsWith('http');
  } else {
    return false;
  }
}
