// Generic Cache Service Interface
interface CacheService {
    get(key: string): string | null;
    set(key: string, value: string): void;
    remove(key: string): void;
    clear(): void;
  }
  
  // Cache Service Implementation Class
class LocalStorageCacheService implements CacheService {
    private ttl: number = 3600;
  
    constructor(ttl: number = 3600) {
      this.ttl = ttl;
    }

    private getExpiryTime(key: string): number {
      return localStorage.getItem(key + '_expiry') ? parseInt(localStorage.getItem(key + '_expiry')!) : 0;
    }

    private setExpiryTime(key: string, expiryTime: number): void {
      localStorage.setItem(key + '_expiry', expiryTime.toString());
    }
  
    get(key: string): string | null {
      const expiryTime = this.getExpiryTime(key);
      if (expiryTime && expiryTime < Date.now()) {
        this.remove(key);
        return null;
      }
      const item = localStorage.getItem(key);
      return item ? item : null;
    }
  
    set(key: string, value: string): void {
      localStorage.setItem(key, value);
      this.setExpiryTime(key, Date.now() + this.ttl);
    }
  
    remove(key: string): void {
      localStorage.removeItem(key);
      localStorage.removeItem(key + '_expiry');
    }
  
    clear(): void {
      localStorage.clear();
    }
}

export { CacheService, LocalStorageCacheService };