export async function SG_EMPLOYEE_COUNT_IMPL_(LINKEDIN_COMPANY_PROFILE_URL: string) {
  check_all_params(
    false,
    ['LINKEDIN_COMPANY_PROFILE_URL'],
    { LINKEDIN_COMPANY_PROFILE_URL }
  );
  LINKEDIN_COMPANY_PROFILE_URL = handleStringParam(
    LINKEDIN_COMPANY_PROFILE_URL
  );
  validateCompanyLinkedinURL(LINKEDIN_COMPANY_PROFILE_URL);
  const proxycurlAPI = new ProxycurlAPI();
  const response = await proxycurlAPI.fetch(
    PCEndpoints.COMPANY_EMPLOYEE_COUNT,
    combine_cache_key(
      LINKEDIN_COMPANY_PROFILE_URL
    ),
    {
      "url": LINKEDIN_COMPANY_PROFILE_URL,
      "function_name": "SG_EMPLOYEE_COUNT",
    }
  );
  return handleResult(response["linkdb_employee_count"]);
}


export async function SG_EMPLOYEES_IMPL_(
  LINKEDIN_COMPANY_PROFILE_URL: string,
  TOTAL_RESULTS: number,
  JOB_TITLE_KEYWORD: string,
  EMPLOYMENT_STATUS: string
) {
  check_all_params(
    true,
    ['LINKEDIN_COMPANY_PROFILE_URL'],
    {
      LINKEDIN_COMPANY_PROFILE_URL,
      TOTAL_RESULTS,
      JOB_TITLE_KEYWORD,
      EMPLOYMENT_STATUS,
    }
  );
  LINKEDIN_COMPANY_PROFILE_URL = handleStringParam(
    LINKEDIN_COMPANY_PROFILE_URL
  );
  validateCompanyLinkedinURL(LINKEDIN_COMPANY_PROFILE_URL);
  TOTAL_RESULTS = handleNumberParam(TOTAL_RESULTS);
  JOB_TITLE_KEYWORD = handleStringParam(JOB_TITLE_KEYWORD, '');
  EMPLOYMENT_STATUS = handleStringParam(EMPLOYMENT_STATUS, 'current');
  const acceptedStatusValues = ['current', 'past', 'all'];
  if (!acceptedStatusValues.includes(EMPLOYMENT_STATUS)) {
    throw new Error('Invalid EMPLOYMENT_STATUS. Accepted values: current [default], past, all');
  }
  const proxycurlAPI = new ProxycurlAPI();
  const response = await proxycurlAPI.fetch(
    PCEndpoints.COMPANY_EMPLOYEE,
    combine_cache_key(
      LINKEDIN_COMPANY_PROFILE_URL,
      TOTAL_RESULTS.toString(),
      JOB_TITLE_KEYWORD,
      EMPLOYMENT_STATUS
    ),
    {
      "url": LINKEDIN_COMPANY_PROFILE_URL,
      "page_size": TOTAL_RESULTS,
      "role_search": JOB_TITLE_KEYWORD,
      "employment_status": EMPLOYMENT_STATUS,
      "function_name": "SG_EMPLOYEES",
    }
  );
  return handleResult(response["employees"].map(item => item.profile_url));
}


export async function SG_LOOKUP_COMPANY_IMPL_(
  COMPANY_NAME_OR_DOMAIN: string,
  LOCATION: string,
) {
  check_all_params(
    true,
    ['COMPANY_NAME_OR_DOMAIN'],
    { COMPANY_NAME_OR_DOMAIN, LOCATION }
  );
  COMPANY_NAME_OR_DOMAIN = handleStringParam(COMPANY_NAME_OR_DOMAIN);
  LOCATION = handleStringParam(LOCATION, '');
  let COMPANY_DOMAIN = '';
  let COMPANY_NAME = '';
  // Check if params domain or name
  const is_domain = COMPANY_NAME_OR_DOMAIN.includes('.');
  if (is_domain) {
    COMPANY_DOMAIN = COMPANY_NAME_OR_DOMAIN;
  } else {
    COMPANY_NAME = COMPANY_NAME_OR_DOMAIN;
  }
  const proxycurlAPI = new ProxycurlAPI();
  const response = await proxycurlAPI.fetch(
    PCEndpoints.COMPANY_LOOKUP,
    combine_cache_key(
      COMPANY_DOMAIN,
      COMPANY_NAME,
      LOCATION,
    ),
    {
      "company_domain": COMPANY_DOMAIN,
      "company_name": COMPANY_NAME,
      "company_location": LOCATION,
      "function_name": "SG_LOOKUP_COMPANY",
    }
  );
  return handleResult(response["url"]);
}


export async function SG_COMPANY_IMPL_(
  LINKEDIN_COMPANY_PROFILE_URL: string,
  COMPANY_ATTRIBUTE: string
) {
  check_all_params(
    false,
    ['LINKEDIN_COMPANY_PROFILE_URL', 'COMPANY_ATTRIBUTE'],
    { LINKEDIN_COMPANY_PROFILE_URL, COMPANY_ATTRIBUTE }
  );
  LINKEDIN_COMPANY_PROFILE_URL = handleStringParam(
    LINKEDIN_COMPANY_PROFILE_URL
  );
  validateCompanyLinkedinURL(LINKEDIN_COMPANY_PROFILE_URL);
  COMPANY_ATTRIBUTE = handleStringParam(COMPANY_ATTRIBUTE);
  const EXCLUDE_ATTRIBUTES = [
    'hq',
    'locations',
    'funding_data',
    'similar_companies',
    'affiliated_companies',
    'acquisitions',
    'exit_data',
    'extra',
  ];

  if (EXCLUDE_ATTRIBUTES.includes(COMPANY_ATTRIBUTE)) {
    throw new Error(`"${COMPANY_ATTRIBUTE}" property does not exist on Company`);
  }

  if (!isKeyOf(DummyCompanyProfileResponse, COMPANY_ATTRIBUTE)) {
    throw new Error(`Invalid value for "${COMPANY_ATTRIBUTE}".`);
  }

  const proxycurlAPI = new ProxycurlAPI();
  const response = await proxycurlAPI.fetch(
    PCEndpoints.COMPANY_PROFILE,
    LINKEDIN_COMPANY_PROFILE_URL,
    {
      "url": LINKEDIN_COMPANY_PROFILE_URL,
      "funding_data": "include",
      "function_name": "SG_COMPANY",
      "_requested_attribute": `${COMPANY_ATTRIBUTE}`,
    }
  );
  if (COMPANY_ATTRIBUTE in response) {
    return handleResult(
      response[COMPANY_ATTRIBUTE as keyof CompanyProfileResponse]
    );
  } else {
    throw new Error(`"${COMPANY_ATTRIBUTE}" property does not exist on Company`);
  }
}


export async function SG_COMPANY_LOCATIONS_IMPL_(
  LINKEDIN_COMPANY_PROFILE_URL: string,
  LOCATION_ATTRIBUTE: string,
  HQ: string | boolean,
  INDEX: number,
) {
  check_all_params(
    false,
    ['LINKEDIN_COMPANY_PROFILE_URL', 'LOCATION_ATTRIBUTE'],
    {
      LINKEDIN_COMPANY_PROFILE_URL,
      LOCATION_ATTRIBUTE,
      HQ,
      INDEX,
    }
  );
  LINKEDIN_COMPANY_PROFILE_URL = handleStringParam(
    LINKEDIN_COMPANY_PROFILE_URL
  );
  validateCompanyLinkedinURL(LINKEDIN_COMPANY_PROFILE_URL);
  LOCATION_ATTRIBUTE = handleStringParam(LOCATION_ATTRIBUTE);
  HQ = handleBooleanParam(HQ, 'true');
  INDEX = handleNumberParam(INDEX, 0);
  if (!isKeyOf(DummyCompanyLocation, LOCATION_ATTRIBUTE)) {
    throw new Error(`Invalid value for "${LOCATION_ATTRIBUTE}".`);
  }
  if (HQ !== 'false' && HQ !== 'true') {
    throw new Error('The valid HQ is only "true" or "false" ');
  }
  const requested_attribute = HQ === 'true' ? `hq,${INDEX},${LOCATION_ATTRIBUTE}` : `locations,${INDEX},${LOCATION_ATTRIBUTE}`;
  const proxycurlAPI = new ProxycurlAPI();
  const response = await proxycurlAPI.fetch(
    PCEndpoints.COMPANY_PROFILE,
    LINKEDIN_COMPANY_PROFILE_URL,
    {
      "url": LINKEDIN_COMPANY_PROFILE_URL,
      "funding_data": "include",
      "function_name": "SG_COMPANY_LOCATIONS",
      "_requested_attribute": requested_attribute,
    }
  );
  if (HQ === 'true') {
    if (!response["hq"]) {
      throw new Error(`The Company has no HQ Location`);
    }
    if (LOCATION_ATTRIBUTE in response["hq"]) {
      return handleResult(
        response["hq"][LOCATION_ATTRIBUTE as keyof CompanyLocation]
      );
    } else {
      throw new Error(`"${LOCATION_ATTRIBUTE}" property does not exist on Company Locations`);
    }
  }
  const Locations = response.locations.filter(
    obj => obj.is_hq === false
  );
  if (Locations.length === 0) {
    throw new Error('The Company has no Locations');
  }
  if (INDEX >= Locations.length) {
    if (Locations.length === 1) {
      throw new Error(`Index out of bounds. This company has only one Location (index=0).`);
    }
    throw new Error(`Index out of bounds: this company's Location supports 0-${Locations.length - 1}.`);
  }

  if (LOCATION_ATTRIBUTE in Locations[INDEX]) {
    return handleResult(
      Locations[INDEX][LOCATION_ATTRIBUTE as keyof CompanyLocation]
    );
  } else {
    throw new Error(`"${LOCATION_ATTRIBUTE}" property does not exist on Company Locations`);
  }
}


export async function SG_COMPANY_FUNDING_IMPL_(
  LINKEDIN_COMPANY_PROFILE_URL: string,
  FUNDING_ATTRIBUTE: string,
  INDEX: number,
) {
  check_all_params(
    false,
    ['LINKEDIN_COMPANY_PROFILE_URL', 'FUNDING_ATTRIBUTE'],
    {
      LINKEDIN_COMPANY_PROFILE_URL,
      FUNDING_ATTRIBUTE,
      INDEX,
    }
  );
  LINKEDIN_COMPANY_PROFILE_URL = handleStringParam(
    LINKEDIN_COMPANY_PROFILE_URL
  );
  validateCompanyLinkedinURL(LINKEDIN_COMPANY_PROFILE_URL);
  FUNDING_ATTRIBUTE = handleStringParam(FUNDING_ATTRIBUTE);
  INDEX = handleNumberParam(INDEX, 0);
  if (!isKeyOf(DummyFunding, FUNDING_ATTRIBUTE)) {
    throw new Error(`Invalid value for "${FUNDING_ATTRIBUTE}".`);
  }
  const proxycurlAPI = new ProxycurlAPI();
  const response = await proxycurlAPI.fetch(
    PCEndpoints.COMPANY_PROFILE,
    LINKEDIN_COMPANY_PROFILE_URL,
    {
      "url": LINKEDIN_COMPANY_PROFILE_URL,
      "funding_data": "include",
      "function_name": "SG_COMPANY_FUNDING",
      "_requested_attribute": `funding_data,${INDEX},${FUNDING_ATTRIBUTE}`,
    }
  );
  if (response.funding_data.length === 0) {
    throw new Error('The Company has no Funding');
  }
  if (INDEX >= response.funding_data.length) {
    if (response.funding_data.length === 1) {
      throw new Error(`Index out of bounds. This company has only one Funding (index=0).`);
    }
    throw new Error(`Index out of bounds: this company's Funding supports 0-${response.funding_data.length - 1}.`);
  }
  const Funding = response.funding_data[INDEX];
  if (FUNDING_ATTRIBUTE in Funding) {
    return handleResult(Funding[FUNDING_ATTRIBUTE as keyof Funding]);
  } else {
    throw new Error(`"${FUNDING_ATTRIBUTE}" property does not exist on Company Funding`);
  }
}


export async function SG_COMPANY_SEARCH_IMPL_(
  COUNTRY: string,
  MAX_RESULT: number,
  PUBLIC_IDENTIFIER_NOT_IN_LIST: string,
  PUBLIC_IDENTIFIER_IN_LIST: string,
  FUNDING_RAISED_BEFORE: string,
  FUNDING_RAISED_AFTER: string,
  FUNDING_AMOUNT_MIN: number,
  FUNDING_AMOUNT_MAX: number,
  FOUNDED_BEFORE_YEAR: number,
  FOUNDED_AFTER_YEAR: number,
  DESCRIPTION: string,
  EMPLOYEE_COUNT_MIN: number,
  EMPLOYEE_COUNT_MAX: number,
  INDUSTRY: string,
  NAME: string,
  FOLLOWER_COUNT_MAX: number,
  FOLLOWER_COUNT_MIN: number,
  TYPE: string,
  CITY: string,
  REGION: string,
) {
  /*eslint prefer-rest-params: "off"*/
  check_all_params(
    true,
    ['COUNTRY'],
    {
      COUNTRY,
      MAX_RESULT,
      PUBLIC_IDENTIFIER_NOT_IN_LIST,
      PUBLIC_IDENTIFIER_IN_LIST,
      FUNDING_RAISED_BEFORE,
      FUNDING_RAISED_AFTER,
      FUNDING_AMOUNT_MIN,
      FUNDING_AMOUNT_MAX,
      FOUNDED_BEFORE_YEAR,
      FOUNDED_AFTER_YEAR,
      DESCRIPTION,
      EMPLOYEE_COUNT_MIN,
      EMPLOYEE_COUNT_MAX,
      INDUSTRY,
      NAME,
      FOLLOWER_COUNT_MAX,
      FOLLOWER_COUNT_MIN,
      TYPE,
      CITY,
      REGION,
    }
  );
  COUNTRY = handleStringParam(COUNTRY, undefined, true);
  MAX_RESULT = handleNumberParam(MAX_RESULT, 10);
  PUBLIC_IDENTIFIER_NOT_IN_LIST = handleStringParam(
    PUBLIC_IDENTIFIER_NOT_IN_LIST, '');
  PUBLIC_IDENTIFIER_IN_LIST = handleStringParam(PUBLIC_IDENTIFIER_IN_LIST, '', true);
  FUNDING_RAISED_BEFORE = handleStringParam(FUNDING_RAISED_BEFORE, '', true);
  FUNDING_RAISED_AFTER = handleStringParam(FUNDING_RAISED_AFTER, '', true);
  FUNDING_AMOUNT_MIN = handleNumberParam(FUNDING_AMOUNT_MIN, 0);
  FUNDING_AMOUNT_MAX = handleNumberParam(FUNDING_AMOUNT_MAX, 0);
  FOUNDED_BEFORE_YEAR = handleNumberParam(FOUNDED_BEFORE_YEAR, 0);
  FOUNDED_AFTER_YEAR = handleNumberParam(FOUNDED_AFTER_YEAR, 0);
  DESCRIPTION = handleStringParam(DESCRIPTION, '', true);
  EMPLOYEE_COUNT_MIN = handleNumberParam(EMPLOYEE_COUNT_MIN, 0);
  EMPLOYEE_COUNT_MAX = handleNumberParam(EMPLOYEE_COUNT_MAX, 0);
  INDUSTRY = handleStringParam(INDUSTRY, '', true);
  NAME = handleStringParam(NAME, '', true);
  FOLLOWER_COUNT_MIN = handleNumberParam(FOLLOWER_COUNT_MIN, 0);
  FOLLOWER_COUNT_MAX = handleNumberParam(FOLLOWER_COUNT_MAX, 0);
  TYPE = handleStringParam(TYPE, '', true);
  const acceptedTypeValues = ['EDUCATIONAL', 'GOVERNMENT_AGENCY', 'NON_PROFIT', 'PARTNERSHIP', 'PRIVATELY_HELD', 'PUBLIC_COMPANY', 'SELF_EMPLOYED', 'SELF_OWNED'];
  if (TYPE != "" && !acceptedTypeValues.includes(TYPE)) {
    throw new Error('Invalid TYPE. Accepted values: EDUCATIONAL, GOVERNMENT_AGENCY, NON_PROFIT, PARTNERSHIP, PRIVATELY_HELD, PUBLIC_COMPANY, SELF_EMPLOYED, SELF_OWNED');
  }
  CITY = handleStringParam(CITY, '', true);
  REGION = handleStringParam(REGION, '', true);

  try {
    const proxycurlAPI = new ProxycurlAPI();
    const response = await proxycurlAPI.fetch(
      PCEndpoints.COMPANY_SEARCH,
      null,
      {
        "country": COUNTRY,
        "page_size": MAX_RESULT,
        "public_identifier_not_in_list": PUBLIC_IDENTIFIER_NOT_IN_LIST,
        "public_identifier_in_list": PUBLIC_IDENTIFIER_IN_LIST,
        "funding_raised_before": FUNDING_RAISED_BEFORE,
        "funding_raised_after": FUNDING_RAISED_AFTER,
        "funding_amount_min": FUNDING_AMOUNT_MIN,
        "funding_amount_max": FUNDING_AMOUNT_MAX,
        "founded_before_year": FOUNDED_BEFORE_YEAR,
        "founded_after_year": FOUNDED_AFTER_YEAR,
        "description": DESCRIPTION,
        "employee_count_min": EMPLOYEE_COUNT_MIN,
        "employee_count_max": EMPLOYEE_COUNT_MAX,
        "industry": INDUSTRY,
        "name": NAME,
        "follower_count_max": FOLLOWER_COUNT_MAX,
        "follower_count_min": FOLLOWER_COUNT_MIN,
        "type": TYPE,
        "city": CITY,
        "region": REGION,
        "function_name": "SG_COMPANY_SEARCH",
      }
    );

    const results: string[] = [];
    response["results"].forEach((result) => {
      results.push(result.linkedin_profile_url);
    });

    return handleResult(results);
  } catch (error) {
    if (error instanceof Error) {
      throw new Error(error.message);
    } else {
      throw new Error(`An error has occured`);
    }
  }
}

