async function wrap_SG_EMPLOYEE_COUNT_IMPL_(LINKEDIN_COMPANY_PROFILE_URL: string) {
  return new Promise(async(resolve: (value: typeof BaseValueObject) => void) => {
    try {
      const resp = await SG_EMPLOYEE_COUNT_IMPL_(LINKEDIN_COMPANY_PROFILE_URL);
      resolve(StringValueObject.create(resp));
    } catch (error) {
      console.log('employee count error', error);
      resolve(StringValueObject.create("Failed to fetch employee count"));
    }
  });
}

async function wrap_SG_EMPLOYEES_IMPL_(LINKEDIN_COMPANY_PROFILE_URL: string, TOTAL_RESULTS: number, JOB_TITLE_KEYWORD: string, EMPLOYMENT_STATUS: string) {
  return new Promise(async(resolve: (value: typeof BaseValueObject) => void) => {
    try {
      const resp = await SG_EMPLOYEES_IMPL_(LINKEDIN_COMPANY_PROFILE_URL, TOTAL_RESULTS, JOB_TITLE_KEYWORD, EMPLOYMENT_STATUS);
      if (Array.isArray(resp)) {
        resolve(ArrayValueObject.createByArray(resp.map(i => [i])));
      } else {
        resolve(StringValueObject.create(resp));
      }
    } catch (error) {
      console.log('employee employees error', error);
      resolve(StringValueObject.create("Failed to fetch employees"));
    }
  });
}

