class SG_EMPLOYEE_COUNT extends AsyncBaseFunction {
  calculate(...variants) {
    this.showLoading();

    if (variants.length !== 1) {
      return StringValueObject.create('Error: Invalid number of parameters');
    }

    const LINKEDIN_COMPANY_PROFILE_URL = variants[0];

    if (LINKEDIN_COMPANY_PROFILE_URL.isError()) {
      return LINKEDIN_COMPANY_PROFILE_URL;
    }

    return new AsyncObject(wrap_SG_EMPLOYEE_COUNT_IMPL_(
      stringFromVariant(LINKEDIN_COMPANY_PROFILE_URL)
    ));
  }
}

class SG_EMPLOYEES extends AsyncBaseFunction {
  calculate(...variants) {
    this.showLoading();

    if (variants.length < 1) {
      return StringValueObject.create('Error: Invalid number of parameters');
    }

    const LINKEDIN_COMPANY_PROFILE_URL = stringFromVariant(variants[0]);

    let TOTAL_RESULTS = 10;
    if (variants.length > 1) {
      TOTAL_RESULTS = intFromVariant(variants[1]);
    }

    let JOB_TITLE_KEYWORD = '';
    if (variants.length > 2) {
      JOB_TITLE_KEYWORD = stringFromVariant(variants[2]);
    }


    let EMPLOYMENT_STATUS = 'current';
    if (variants.length > 3) {
      EMPLOYMENT_STATUS = stringFromVariant(variants[3]);
    }

    return new AsyncObject(wrap_SG_EMPLOYEES_IMPL_(
      LINKEDIN_COMPANY_PROFILE_URL,
      TOTAL_RESULTS,
      JOB_TITLE_KEYWORD,
      EMPLOYMENT_STATUS
    ));
  }
}

