import { registerFormulaFunction } from "../register";

// @ts-ignore
const {
  FunctionType,
  BaseFunction, 
  NumberValueObject,
  StringValueObject,
  BaseValueObject,
  AsyncObject
} = window.UniverEngineFormula;

import {
    combine_cache_key,
    handleResult,
    check_all_params,
    validatePersonLinkedinURL,
} from "../../utils/common";
import { 
  ProxycurlAPI, 
  PCEndpoints 
} from "../../utils/proxycurl_api";
import { stringFromVariant } from "./util";
import { AsyncBaseFunction } from "./base";
  
/*eslint prefer-rest-params: "off"*/

const enum SG_CONTACT_FORMULA_NAMES {
  SG_PERSONAL_NUMBERS = 'SG_PERSONAL_NUMBERS',
  SG_PERSONAL_EMAILS = 'SG_PERSONAL_EMAILS',
}

export async function SG_PERSONAL_NUMBERS_IMPL_(LINKEDIN_PROFILE_URL: string) {
    check_all_params(
      false,
      ['LINKEDIN_PROFILE_URL'],
      { LINKEDIN_PROFILE_URL }
    );
    validatePersonLinkedinURL(LINKEDIN_PROFILE_URL);
    const proxycurlAPI = new ProxycurlAPI();
    const response = await proxycurlAPI.fetch(
      PCEndpoints.CONTACT_PERSONAL_NUMBER,
      combine_cache_key(
        LINKEDIN_PROFILE_URL
      ),
      {
        "linkedin_profile_url": LINKEDIN_PROFILE_URL,
        'function_name': 'SG_PERSONAL_NUMBERS',
      }
    );
    return handleResult(response["numbers"]);
  }
  
  export async function SG_PERSONAL_EMAILS_IMPL_(LINKEDIN_PROFILE_URL: string) {
    check_all_params(
      false,
      ['LINKEDIN_PROFILE_URL'],
      { LINKEDIN_PROFILE_URL }
    );
    validatePersonLinkedinURL(LINKEDIN_PROFILE_URL);
    const proxycurlAPI = new ProxycurlAPI();
    const response = await proxycurlAPI.fetch(
      PCEndpoints.PERSONAL_EMAIL_PROFILE,
      combine_cache_key(
        LINKEDIN_PROFILE_URL
      ),
      {
        "linkedin_profile_url": LINKEDIN_PROFILE_URL,
        "email_validation":"fast",
        'function_name': 'SG_PERSONAL_EMAILS',
      }
    );
    return handleResult(response["emails"]);
  }


async function wrap_SG_PERSONAL_NUMBERS_IMPL_(LINKEDIN_PROFILE_URL: string) {
  return new Promise(async(resolve: (value: typeof BaseValueObject) => void) => {
    try {
      const resp = await SG_PERSONAL_NUMBERS_IMPL_(LINKEDIN_PROFILE_URL);
      resolve(StringValueObject.create(resp));
    } catch (error) {
      resolve(StringValueObject.create("Failed to fetch personal numbers"));
    }
  });
}

async function wrap_SG_PERSONAL_EMAILS_IMPL_(LINKEDIN_PROFILE_URL: string) {
  return new Promise(async(resolve: (value: typeof BaseValueObject) => void) => {
    try {
      const resp = await SG_PERSONAL_EMAILS_IMPL_(LINKEDIN_PROFILE_URL);
      resolve(StringValueObject.create(resp));
    } catch (error) {
      resolve(StringValueObject.create("Failed to fetch personal emails"));
    }
  });
}

class SG_PERSONAL_NUMBERS extends AsyncBaseFunction {
  calculate(...variants) {
    this.showLoading();
    if (variants.length < 1) {
      return StringValueObject.create('Error: Invalid number of parameters');
    }
    const LINKEDIN_PROFILE_URL = stringFromVariant(variants[0]);
    return new AsyncObject(wrap_SG_PERSONAL_NUMBERS_IMPL_(LINKEDIN_PROFILE_URL));
  }
}

class SG_PERSONAL_EMAILS extends AsyncBaseFunction {
  calculate(...variants) {
    this.showLoading();
    if (variants.length < 1) {
      return StringValueObject.create('Error: Invalid number of parameters');
    }
    const LINKEDIN_PROFILE_URL = stringFromVariant(variants[0]);
    return new AsyncObject(wrap_SG_PERSONAL_EMAILS_IMPL_(LINKEDIN_PROFILE_URL));
  }
}

registerFormulaFunction({
  function: SG_PERSONAL_NUMBERS,
  locale: {
    description: "Returns a comma-separated list of personal contact numbers given a LinkedIn profile. Cost: 10 credits per contact number returned.",
    abstract: "Get personal contact numbers from LinkedIn profile",
    functionParameter: {
      LINKEDIN_PROFILE_URL: {
        name: "LINKEDIN_PROFILE_URL",
        detail: "LinkedIn Profile URL of the target profile (e.g., https://www.linkedin.com/in/williamhgates/)"
      }
    }
  },
  definition: {
    functionName: SG_CONTACT_FORMULA_NAMES.SG_PERSONAL_NUMBERS,
    aliasFunctionName: "sapiengraph.SG_PERSONAL_NUMBERS.aliasFunctionName",
    description: "sapiengraph.SG_PERSONAL_NUMBERS.description",
    abstract: "sapiengraph.SG_PERSONAL_NUMBERS.abstract",
    functionParameter: [
      {
        name: "sapiengraph.SG_PERSONAL_NUMBERS.functionParameter.LINKEDIN_PROFILE_URL.name",
        detail: "sapiengraph.SG_PERSONAL_NUMBERS.functionParameter.LINKEDIN_PROFILE_URL.detail",
        example: "https://www.linkedin.com/in/williamhgates/",
        require: 1,
        repeat: 0,
      }
    ]
  }
})

