// @ts-ignore
const { 
    BaseValueObject 
} = window.UniverEngineFormula;

export function stringFromVariant(variant: typeof BaseValueObject): string {
    if (variant.isError()) {
        return '';
    }

    if (variant.isString()) {
        return variant.getValue();
    }

    if (variant.isNumber()) {
        return variant.getValue().toString();
    }

    if (variant.isArray()) {
        return variant.getValueOrDefault(0, 0)?.getValue();
    }
    
    return '';
}

export function intFromVariant(variant: typeof BaseValueObject): number {
    if (variant.isError()) {
        return 0;
    }

    if (variant.isNumber()) {
        return variant.getValue();
    }

    if (variant.isString()) {
        return parseInt(variant.getValue());
    }

    if (variant.isArray()) {
        return variant.getValueOrDefault(0, 0)?.getValue();
    }

    return 0;
}

export function dateStringFromVariant(variant: typeof BaseValueObject): string {
    if (variant.isError()) {
        return '';
    }

    if (variant.isString()) {
        return variant.getValue();
    }

    // Excel serial number
    if (variant.isNumber()) {
        return excelDateToString(variant.getValue());
    }

    return '';
}

function excelDateToString(serial: number): string {
    const baseDate = new Date(1899, 11, 30); // Excel's date system starts on 1900-01-01, serial number 1
    const date = new Date(baseDate.getTime() + serial * 24 * 60 * 60 * 1000); // Add days in milliseconds
    return date.toISOString().split('T')[0]; // Format as 'YYYY-MM-DD'
}