// @ts-ignore
const { BaseFunction } = window.UniverEngineFormula;

export class AsyncBaseFunction extends BaseFunction {
    protected loadingMessage: string = 'Loading...';
    protected showLoading() {
        if (!window.UniverAPI) {
            return;
        }

        try {
            const sheet = window.UniverAPI.getActiveWorkbook().getActiveSheet();
            if (!sheet) {
                return;
            }

            const range = sheet.getRange(this.row, this.column);
            if (!range) {
                return;
            }

            range.setValue(this.loadingMessage);
        } catch (error) {
            console.error('Error showing loading', error);
        }
    }

    isAsync() {
        return true;
    }
}