declare global {
    interface Window {
      UniverCore: any;
      UniverSheetsFormula: any;
      UniverUMD: {
        'en-US': any;
      };
      UniverAPI: any;
    }
  }
  
  const {
    UniverCore,
    UniverDesign,
    UniverEngineRender,
    UniverEngineFormula,
    UniverDocs,
    UniverDocsUi,
    UniverUi,
    UniverSheets,
    UniverSheetsUi,
    UniverSheetsNumfmt
  } = window;
  
  const { UniverSheetsFormulaPlugin } = UniverSheetsFormula;
  const { UniverSheetsNumfmtPlugin } = UniverSheetsNumfmt;
  const { UniverDocsPlugin } = UniverDocs;
  const { UniverRenderEnginePlugin } = UniverEngineRender;
  const { UniverFormulaEnginePlugin } = UniverEngineFormula;
  const { UniverSheetsFormulaUIPlugin } = UniverSheetsFormulaUi;
  const { UniverFindReplacePlugin } = UniverFindReplace;
  const { UniverSheetsFindReplacePlugin } = UniverSheetsFindReplace;
  const { UniverSheetsZenEditorPlugin } = UniverSheetsZenEditor;
  const { UniverUIPlugin } = UniverUi;
  const { UniverDocsUIPlugin } = UniverDocsUi;
  const { UniverSheetsPlugin } = UniverSheets;
  const { UniverSheetsUIPlugin } = UniverSheetsUi;
  const { defaultTheme, greenTheme } = UniverDesign;
  const { FUniver } = UniverFacade;
  const {
    UniverInstanceType,
    Tools,
    IUndoRedoService,
    IAuthzIoService,
    LocaleType,
    Univer,
    IConfigService
  } = window.UniverCore;
  import {
    SG_DEFINITIONS,
    SG_FORMULA_FUNCTIONS, 
    SG_FORMULA_FUNCTIONS_MAP 
  } from './formula';

  console.log('SG_DEFINITIONS');
  console.log(SG_DEFINITIONS);
  
  interface UniverConfig {
    theme: typeof defaultTheme;
    locale: any;
    locales: {
      [key: string]: any[];
    };
  }
  
  interface PluginConfig {
    container: string;
    header: boolean;
    footer: boolean;
  }
  
  const setup = (): any => {
    const univer = new Univer({
      theme: defaultTheme,
      locale: LocaleType.EN_US,
      locales: {
        [LocaleType.EN_US]: Tools.deepMerge(
          UniverUMD['en-US'], 
          SG_DEFINITIONS
        ),
      }
    });
  
    univer.registerPlugin(UniverRenderEnginePlugin);
    univer.registerPlugin(UniverDocsPlugin);
    univer.registerPlugin(UniverFormulaEnginePlugin, {
      function: SG_FORMULA_FUNCTIONS_MAP,
    });
    univer.registerPlugin(UniverSheetsFormulaPlugin);
    univer.registerPlugin(UniverSheetsFormulaUIPlugin, {
      description: SG_FORMULA_FUNCTIONS,
    });
  
    univer.registerPlugin(UniverUIPlugin, {
      container: "spreadsheet-container",
      header: true,
      footer: true,
    });
  
    univer.registerPlugin(UniverDocsUIPlugin);
  
    univer.registerPlugin(UniverSheetsPlugin);
    univer.registerPlugin(UniverSheetsUIPlugin);
    univer.registerPlugin(UniverFindReplacePlugin);
    univer.registerPlugin(UniverSheetsFindReplacePlugin);
    univer.registerPlugin(UniverSheetsNumfmtPlugin);
    univer.registerPlugin(UniverSheetsZenEditorPlugin);
  
    univer.createUnit(UniverInstanceType.UNIVER_SHEET, {});
  
    return FUniver.newAPI(univer);
  };
  
  const univerAPI = setup();
  if (window) {
    window.UniverAPI = univerAPI;
  }
  
  export { univerAPI };