export default class Config {
  API_KEY: string;
  PX_API_BASE_URL: string;
  API_BASE_URL: string;
  CACHE_TTL: number;
  PX_CLOUDFLARE_ACCESS_CLIENT_ID: string;
  PX_CLOUDFLARE_ACCESS_CLIENT_SECRET: string;

  constructor() {
    this.API_KEY = getCookie('sg_token') || '';
    this.PX_API_BASE_URL = getProxycurlBaseURL((document.getElementById('sg_px_api_base_url') as HTMLInputElement)?.value || 'https://nubela.co/proxycurl-dev');
    this.API_BASE_URL = `${getScheme()}://${(document.getElementById('sg_api_base_url') as HTMLInputElement)?.value || 'sapiengraph.com'}`;
    this.CACHE_TTL = parseInt((document.getElementById('sg_cache_ttl') as HTMLInputElement)?.value || '3600');
    this.PX_CLOUDFLARE_ACCESS_CLIENT_ID = (document.getElementById('px_cf_access_client_id') as HTMLInputElement)?.value || '';
    this.PX_CLOUDFLARE_ACCESS_CLIENT_SECRET = (document.getElementById('px_cf_access_client_secret') as HTMLInputElement)?.value || '';
  }
}

function getCookie(name: string): string | null {
  const value = `; ${document.cookie}`;
  const parts = value.split(`; ${name}=`);
  if (parts.length === 2) {
      return parts.pop()?.split(";").shift() || null;
  }
  return null;
}

function getScheme(): string {
  return window.location.protocol;
}

function getProxycurlBaseURL(baseURL: string): string {
  if (baseURL.includes('nubela.co')) {
    return `https://${baseURL}`;
  }

  return `http://${baseURL}`;
}