export async function SG_PERSON_IMPL_(
  PROFILE_URL: string,
  PERSON_ATTRIBUTE: string
) {
  check_all_params(
    false,
    ['PROFILE_URL', 'PERSON_ATTRIBUTE'],
    { PROFILE_URL, PERSON_ATTRIBUTE }
  );

  PROFILE_URL = handleStringParam(PROFILE_URL);
  const PROFILE_TYPE: SocialMediaType = validateAndCheckSocialMediaURL(
    PROFILE_URL
  );
  PERSON_ATTRIBUTE = handleStringParam(PERSON_ATTRIBUTE);
  type attributeAliasesType = {
    [key: string]: string;
  };
  const attributeAliases: attributeAliasesType = {
    connection_count: 'connections',
    github_profile_id: 'extra,github_profile_id',
    twitter_profile_id: 'extra,twitter_profile_id',
    facebook_profile_id: 'extra,facebook_profile_id',
    inferred_salary_min: 'inferred_salary,min',
    inferred_salary_max: 'inferred_salary,max',
  };
  if (!isKeyOf(DummyPersonProfileResponse, PERSON_ATTRIBUTE) &&
    !(PERSON_ATTRIBUTE in attributeAliases)) {
    throw new Error(`Invalid value for "${PERSON_ATTRIBUTE}".`);
  }
  let requested_attribute = PERSON_ATTRIBUTE;
  if (PERSON_ATTRIBUTE in attributeAliases){
    const arrayAttributeAliases = attributeAliases[PERSON_ATTRIBUTE].split(',');
    if (arrayAttributeAliases.length == 2){
      const [attribute1, attribute2]  = arrayAttributeAliases;
      requested_attribute= `${attribute1},${attribute2}`;
    }
  }
  const proxycurlAPI = new ProxycurlAPI();
  const response = await proxycurlAPI.fetch(
    PCEndpoints.PERSON_PROFILE,
    PROFILE_URL,
    {
      [SNSProfile[PROFILE_TYPE]] : PROFILE_URL,
      "github_profile_id": "include",
      "facebook_profile_id": "include",
      "twitter_profile_id": "include",
      'inferred_salary': 'include',
      "function_name": "SG_PERSON",
      "_requested_attribute": requested_attribute,
    }
  );
  if (PERSON_ATTRIBUTE in response || PERSON_ATTRIBUTE in attributeAliases) {
    if (PERSON_ATTRIBUTE in attributeAliases) {
      const arrayAttributeAliases = attributeAliases[PERSON_ATTRIBUTE].split(',');
      if (arrayAttributeAliases.length == 1) {
        PERSON_ATTRIBUTE = attributeAliases[PERSON_ATTRIBUTE];
      } else if (arrayAttributeAliases.length == 2) {
        const [attribute1, attribute2]  = arrayAttributeAliases;
        if (attribute1 == 'extra') {
          const result = response[
            attribute1 as keyof PersonProfileResponse
          ] as PersonExtra;
          if(result) {
            return handleResult(
              result[attribute2 as keyof PersonExtra]
            );
          } else {
            throw new Error(`"${PERSON_ATTRIBUTE}" property does not exist on Person`);
          }
        } else if (attribute1 == 'inferred_salary') {
          const result = response[
            attribute1 as keyof PersonProfileResponse
          ] as InferredSalary;
          if (result) {
            return handleResult(
              result[attribute2 as keyof InferredSalary]
            );
          } else {
            throw new Error(`"${PERSON_ATTRIBUTE}" property does not exist on Person`);
          }
        }
      }

    }
    if (PERSON_ATTRIBUTE in attributeAliases) {
      PERSON_ATTRIBUTE = attributeAliases[PERSON_ATTRIBUTE];
    }
    return handleResult(
      response[PERSON_ATTRIBUTE as keyof PersonProfileResponse]
    );
  } else {
    throw new Error(`"${PERSON_ATTRIBUTE}" property does not exist on Person`);
  }
}


export async function SG_PERSON_WORK_IMPL_(
  LINKEDIN_PROFILE_URL: string,
  WORK_HISTORY_ATTRIBUTE: string,
  INDEX: number
) {
  check_all_params(
    false,
    ['LINKEDIN_PROFILE_URL', 'WORK_HISTORY_ATTRIBUTE'],
    { LINKEDIN_PROFILE_URL, WORK_HISTORY_ATTRIBUTE, INDEX }
  );
  LINKEDIN_PROFILE_URL = handleStringParam(LINKEDIN_PROFILE_URL);
  validatePersonLinkedinURL(LINKEDIN_PROFILE_URL);
  WORK_HISTORY_ATTRIBUTE = handleStringParam(WORK_HISTORY_ATTRIBUTE);
  INDEX = handleNumberParam(INDEX, 0);
  if (!isKeyOf(DummyWorkHistoryResponse, WORK_HISTORY_ATTRIBUTE)) {
    throw new Error(`Invalid value for "${WORK_HISTORY_ATTRIBUTE}".`);
  }
  const proxycurlAPI = new ProxycurlAPI();
  const response = await proxycurlAPI.fetch(
    PCEndpoints.PERSON_PROFILE,
    LINKEDIN_PROFILE_URL,
    {
      "linkedin_profile_url": LINKEDIN_PROFILE_URL,
      "github_profile_id": "include",
      "facebook_profile_id": "include",
      "twitter_profile_id": "include",
      'inferred_salary': 'include',
      "function_name": "SG_PERSON_WORK",
      "_requested_attribute": `experiences,${INDEX},${WORK_HISTORY_ATTRIBUTE}`,
    }
  );
  if (response.experiences.length === 0) {
    throw new Error("The Person doesn't have Work History.");
  }
  if (INDEX >= response.experiences.length) {
    if (response.experiences.length === 1) {
      throw new Error(`Index out of bounds. This company has only one Work History (index=0).`);
    }
    throw new Error(`Index out of bounds: this company's Work History supports 0-${response.experiences.length - 1}.`);
  }
  const WorkHistory = response.experiences[INDEX];
  if (WORK_HISTORY_ATTRIBUTE in WorkHistory) {
    return handleResult(
      WorkHistory[WORK_HISTORY_ATTRIBUTE as keyof WorkHistoryResponse]
    );
  } else {
    throw new Error(`"${WORK_HISTORY_ATTRIBUTE}" property does not exist on Person`);
  }
}


