export async function SG_LOOKUP_PERSON_IMPL_(
  FIRST_NAME: string,
  COMPANY: string,
  LAST_NAME: string,
  LOCATION: string,
  TITLE: string,
) {
  check_all_params(
    true,
    ['FIRST_NAME', 'COMPANY'],
    { FIRST_NAME, COMPANY, LAST_NAME, LOCATION, TITLE }
  );
  FIRST_NAME = handleStringParam(FIRST_NAME);
  COMPANY = handleStringParam(COMPANY);
  LAST_NAME = handleStringParam(LAST_NAME, '');
  LOCATION = handleStringParam(LOCATION, '');
  TITLE = handleStringParam(TITLE, '');
  const proxycurlAPI = new ProxycurlAPI();
  const response = await proxycurlAPI.fetch(
    PCEndpoints.PERSON_LOOKUP,
    combine_cache_key(
      FIRST_NAME,
      LAST_NAME,
      LOCATION,
      TITLE,
      COMPANY
    ),
    {
      "first_name": FIRST_NAME,
      "last_name": LAST_NAME,
      "location": LOCATION,
      "title": TITLE,
      "company_domain": COMPANY,
      "function_name": "SG_LOOKUP_PERSON",
    }
  );
  return handleResult(response["url"]);
}


