export async function SG_STUDENT_LISTING_IMPL_(
  LINKEDIN_SCHOOL_URL: string,
  MAX_RESULT: number,
  KEYWORD: string,
  COUNTRY: string,
  STATUS: string,
  SORT_BY: string
) {
  check_all_params(
    true,
    ['LINKEDIN_SCHOOL_URL'],
    {
      LINKEDIN_SCHOOL_URL,
      MAX_RESULT,
      KEYWORD,
      COUNTRY,
      STATUS,
      SORT_BY,
    }
  );
  LINKEDIN_SCHOOL_URL = handleStringParam(
    LINKEDIN_SCHOOL_URL
  );
  validateSchoolLinkedinURL(LINKEDIN_SCHOOL_URL);
  MAX_RESULT = handleNumberParam(MAX_RESULT, 10);
  if (!(MAX_RESULT >= 1 && MAX_RESULT <= 200000)) {
    throw new Error('Invalid NUMBER.Accepted values is within the range of 1 and 200000');
  }
  KEYWORD = handleStringParam(KEYWORD, '');
  COUNTRY = handleStringParam(COUNTRY, '');
  STATUS = handleStringParam(STATUS, 'current');
  const acceptedStatusValues = ['current', 'past', 'all'];
  if (!acceptedStatusValues.includes(STATUS)) {
    throw new Error('Invalid STATUS. Accepted values: current [default], past, all');
  }
  SORT_BY = handleStringParam(SORT_BY, 'none');
  const acceptedSortValues = ['none', 'recently-matriculated', 'recently-graduated'];
  if (!acceptedSortValues.includes(SORT_BY)) {
    throw new Error('Invalid STATUS. Accepted values: none [default], recently-matriculated, recently-graduated');
  }

  const proxycurlAPI = new ProxycurlAPI();
  const response = await proxycurlAPI.fetch(
    PCEndpoints.STUDENT_LISTING,
    combine_cache_key(
      LINKEDIN_SCHOOL_URL,
      MAX_RESULT.toString(),
      KEYWORD,
      COUNTRY,
      STATUS,
      SORT_BY
    ),
    {
      "linkedin_school_url": LINKEDIN_SCHOOL_URL,
      "page_size": MAX_RESULT,
      "search_keyword": KEYWORD,
      "country": COUNTRY,
      "student_status": STATUS,
      "sort_by": SORT_BY,
      "function_name": "SG_STUDENT_LISTING",
    }
  );
  return handleResult(response["students"].map(item =>item.profile_url));
}


export async function SG_SCHOOL_IMPL_(
  LINKEDIN_PROFILE_URL: string,
  SCHOOL_ATTRIBUTE: string
) {
  check_all_params(
    false,
    ['LINKEDIN_PROFILE_URL', 'SCHOOL_ATTRIBUTE'],
    {LINKEDIN_PROFILE_URL, SCHOOL_ATTRIBUTE}
  );

  LINKEDIN_PROFILE_URL = handleStringParam(LINKEDIN_PROFILE_URL);
  validateSchoolLinkedinURL(LINKEDIN_PROFILE_URL);
  SCHOOL_ATTRIBUTE = handleStringParam(SCHOOL_ATTRIBUTE);
  if (!isKeyOf(DummySchoolProfileResponse, SCHOOL_ATTRIBUTE)) {
    throw new Error(`Invalid value for "${SCHOOL_ATTRIBUTE}".`);
  }
  const proxycurlAPI = new ProxycurlAPI();
  const response = await proxycurlAPI.fetch(
    PCEndpoints.SCHOOL_PROFILE,
    LINKEDIN_PROFILE_URL,
    {
      "url": LINKEDIN_PROFILE_URL,
      "function_name": "SG_SCHOOL",
    }
  );
  if (SCHOOL_ATTRIBUTE in response) {

    return handleResult(
      response[SCHOOL_ATTRIBUTE as keyof SchoolProfileResponse]
    );
  } else {
    throw new Error(`"${SCHOOL_ATTRIBUTE}" property does not exist on School`);
  }
}


export async function SG_SCHOOL_LOCATIONS_IMPL_(
  LINKEDIN_PROFILE_URL: string,
  LOCATION_ATTRIBUTE: string,
  INDEX: number,
) {
  check_all_params(
    false,
    ['LINKEDIN_PROFILE_URL', 'LOCATION_ATTRIBUTE'],
    {
      LINKEDIN_PROFILE_URL,
      LOCATION_ATTRIBUTE,
      INDEX,
    }
  );
  LINKEDIN_PROFILE_URL = handleStringParam(
    LINKEDIN_PROFILE_URL
  );
  validateSchoolLinkedinURL(LINKEDIN_PROFILE_URL);
  LOCATION_ATTRIBUTE = handleStringParam(LOCATION_ATTRIBUTE);
  INDEX = handleNumberParam(INDEX, 0);
  if (!isKeyOf(DummySchoolLocation, LOCATION_ATTRIBUTE)) {
    throw new Error(`Invalid value for "${LOCATION_ATTRIBUTE}".`);
  }
  const proxycurlAPI = new ProxycurlAPI();
  const response = await proxycurlAPI.fetch(
    PCEndpoints.SCHOOL_PROFILE,
    LINKEDIN_PROFILE_URL,
    {
      "url": LINKEDIN_PROFILE_URL,
      "function_name": "SG_SCHOOL_LOCATIONS",
    }
  );
  const Locations = response.locations;
  if ( Locations.length === 0) {
    throw new Error('The School has no Locations');
  }
  if (INDEX >= Locations.length) {
    if(Locations.length === 1){
      throw new Error(`Index out of bounds. This school has only one Location (index=0).`);
    }
    throw new Error(`Index out of bounds: This school's Location supports 0-${Locations.length - 1}.`);
  }

  if (LOCATION_ATTRIBUTE in Locations[INDEX]) {
    return handleResult(
      Locations[INDEX][LOCATION_ATTRIBUTE as keyof SchoolLocation]
    );
  } else {
    throw new Error(`"${LOCATION_ATTRIBUTE}" property does not exist on School Locations`);
  }
}

async function wrap_SG_STUDENT_LISTING_IMPL_(
  LINKEDIN_PROFILE_URL: string,
  MAX_RESULT: number,
  KEYWORD: string,
  COUNTRY: string,
  STATUS: string,
  SORT_BY: string,
) {
  return new Promise(async(resolve: (value: typeof BaseValueObject) => void) => {
    try {
      const resp = await SG_STUDENT_LISTING_IMPL_(
        LINKEDIN_PROFILE_URL, MAX_RESULT, KEYWORD, COUNTRY, STATUS, SORT_BY
      );
      if (Array.isArray(resp)) {
        resolve(ArrayValueObject.createByArray(resp.map(i => [i])));
      } else {
        resolve(StringValueObject.create(resp));
      }
    } catch (error) {
      resolve(StringValueObject.create("Failed to fetch student listing"));
    }
  });
}

async function wrap_SG_SCHOOL_IMPL_(
  LINKEDIN_PROFILE_URL: string,
  SCHOOL_ATTRIBUTE: string,
) {
  return new Promise(async(resolve: (value: typeof BaseValueObject) => void) => {
    try {
      const resp = await SG_SCHOOL_IMPL_(LINKEDIN_PROFILE_URL, SCHOOL_ATTRIBUTE);
      resolve(StringValueObject.create(resp));
    } catch (error) {
      resolve(StringValueObject.create("Failed to fetch school"));
    }
  });
} 

async function wrap_SG_SCHOOL_LOCATIONS_IMPL_(
  LINKEDIN_PROFILE_URL: string,
  LOCATION_ATTRIBUTE: string,
  INDEX: number,
) {
  return new Promise(async(resolve: (value: typeof BaseValueObject) => void) => {
    try {
      const resp = await SG_SCHOOL_LOCATIONS_IMPL_(
        LINKEDIN_PROFILE_URL, LOCATION_ATTRIBUTE, INDEX
      );
      resolve(resp);
    } catch (error) {
      resolve(StringValueObject.create("Failed to fetch school locations"));
    }
  });
} 

class SG_STUDENT_LISTING extends AsyncBaseFunction {
  calculate(...variants) {
    this.showLoading();
    if (variants.length < 1) {
      return StringValueObject.create('Error: Invalid number of parameters');
    }

    const LINKEDIN_PROFILE_URL = stringFromVariant(variants[0]);

    let MAX_RESULT = 10;
    if (variants.length > 1) {
      MAX_RESULT = intFromVariant(variants[1]);
    }

    let KEYWORD = '';
    if (variants.length > 2) {
      KEYWORD = stringFromVariant(variants[2]);
    }

    let COUNTRY = '';
    if (variants.length > 3) {
      COUNTRY = stringFromVariant(variants[3]);
    }

    let STATUS = 'current';
    if (variants.length > 4) {
      STATUS = stringFromVariant(variants[4]);
    }

    let SORT_BY = 'none';
    if (variants.length > 5) {
      SORT_BY = stringFromVariant(variants[5]);
    }

    return new AsyncObject(wrap_SG_STUDENT_LISTING_IMPL_(
      LINKEDIN_PROFILE_URL, MAX_RESULT, KEYWORD, COUNTRY, STATUS, SORT_BY
    ));
  }
} 

class SG_SCHOOL extends AsyncBaseFunction {
  calculate(...variants) {
    this.showLoading();
    if (variants.length < 2) {
      return StringValueObject.create('Error: Invalid number of parameters');
    }

    const LINKEDIN_PROFILE_URL = stringFromVariant(variants[0]);
    const SCHOOL_ATTRIBUTE = stringFromVariant(variants[1]);

    return new AsyncObject(wrap_SG_SCHOOL_IMPL_(
      LINKEDIN_PROFILE_URL, SCHOOL_ATTRIBUTE
    ));
  }
} 

class SG_SCHOOL_LOCATIONS extends AsyncBaseFunction {
  calculate(...variants) {
    this.showLoading();
    if (variants.length < 2) {
      return StringValueObject.create('Error: Invalid number of parameters');
    }

    const LINKEDIN_PROFILE_URL = stringFromVariant(variants[0]);
    const LOCATION_ATTRIBUTE = stringFromVariant(variants[1]);

    let INDEX = 0;
    if (variants.length > 2) {
      INDEX = intFromVariant(variants[2]);
    }

    return new AsyncObject(wrap_SG_SCHOOL_LOCATIONS_IMPL_(
      LINKEDIN_PROFILE_URL, LOCATION_ATTRIBUTE, INDEX
    ));
  }
}

