import { CacheService, LocalStorageCacheService } from './cache_service';
import Config from './config';

export enum PCEndpoints {
  PERSON_PROFILE = '/api/v2/linkedin',
  PERSON_LOOKUP = '/api/linkedin/profile/resolve',
  PERSONAL_EMAIL_PROFILE = '/api/contact-api/personal-email',
  REVERSE_EMAIL_LOOKUP = '/api/linkedin/profile/resolve/email',
  ROLE_LOOKUP = '/api/find/company/role',
  COMPANY_EMPLOYEE_COUNT = '/api/linkedin/company/employees/count',
  COMPANY_EMPLOYEE = '/api/linkedin/company/employees',
  COMPANY_LOOKUP = '/api/linkedin/company/resolve',
  COMPANY_PROFILE = '/api/linkedin/company',
  CONTACT_PERSONAL_NUMBER = '/api/contact-api/personal-contact',
  JOB = '/api/linkedin/job',
  JOBS_COUNT = '/api/v2/linkedin/company/job/count',
  STUDENT_LISTING = '/api/linkedin/school/students',
  SCHOOL_PROFILE = '/api/linkedin/school',
  PERSON_SEARCH = '/api/v2/search/person',
  COMPANY_SEARCH = '/api/v2/search/company',
  JOBS_SEARCH = '/api/v2/linkedin/company/job',
}

export const DynamicCostPCEndpoints: Record<string, string>= {
  [PCEndpoints.PERSONAL_EMAIL_PROFILE]: 'emails',
  [PCEndpoints.COMPANY_EMPLOYEE]: 'employees',
  [PCEndpoints.CONTACT_PERSONAL_NUMBER]: 'numbers',
  [PCEndpoints.STUDENT_LISTING]: 'students',
  [PCEndpoints.JOBS_SEARCH]: 'job',
};

const PCCacheKey = {
  [PCEndpoints.PERSON_PROFILE]: 'person-',
  [PCEndpoints.PERSON_LOOKUP]: 'person-lookup-',
  [PCEndpoints.PERSONAL_EMAIL_PROFILE]: 'personal-email-',
  [PCEndpoints.REVERSE_EMAIL_LOOKUP]: 'reverse-email-lookup-',
  [PCEndpoints.ROLE_LOOKUP]: 'role-lookup-',
  [PCEndpoints.COMPANY_EMPLOYEE_COUNT]: 'company-employee-count-',
  [PCEndpoints.COMPANY_EMPLOYEE]: 'company-employee-',
  [PCEndpoints.COMPANY_LOOKUP]: 'company-lookup-',
  [PCEndpoints.COMPANY_PROFILE]: 'company-',
  [PCEndpoints.CONTACT_PERSONAL_NUMBER]: 'contact-personal-number-',
  [PCEndpoints.JOB]: 'job-',
  [PCEndpoints.JOBS_COUNT]: 'jobs-count-',
  [PCEndpoints.STUDENT_LISTING]: 'student-listing-',
  [PCEndpoints.SCHOOL_PROFILE]: 'student-profile-',
  [PCEndpoints.PERSON_SEARCH]: 'person-search-',
  [PCEndpoints.COMPANY_SEARCH]: 'company-search-',
  [PCEndpoints.JOBS_SEARCH]: 'jobs-search-',
};

