type ResponseFromEndpoint<T extends PCEndpoints> =
  T extends typeof PCEndpoints.PERSON_PROFILE
  ? PersonProfileResponse
  : T extends typeof PCEndpoints.PERSON_LOOKUP
  ? PersonLookupResponse
  : T extends typeof PCEndpoints.REVERSE_EMAIL_LOOKUP
  ? ReverseEmailLookupResponse
  : T extends typeof PCEndpoints.ROLE_LOOKUP
  ? RoleLookupResponse
  : T extends typeof PCEndpoints.COMPANY_EMPLOYEE_COUNT
  ? CompanyEmployeeCountResponse
  : T extends typeof PCEndpoints.COMPANY_EMPLOYEE
  ? CompanyEmployeeResponse
  : T extends typeof PCEndpoints.COMPANY_LOOKUP
  ? CompanyLookupResponse
  : T extends typeof PCEndpoints.COMPANY_PROFILE
  ? CompanyProfileResponse
  : T extends typeof PCEndpoints.CONTACT_PERSONAL_NUMBER
  ? ContactPersonalNumberResponse
  : T extends typeof PCEndpoints.PERSONAL_EMAIL_PROFILE
  ? PersonalEmailResponse
  : T extends typeof PCEndpoints.STUDENT_LISTING
  ? StudentListingResponse
  : T extends typeof PCEndpoints.SCHOOL_PROFILE
  ? SchoolProfileResponse
  : T extends typeof PCEndpoints.JOB
  ? JobProfileResponse
  : T extends typeof PCEndpoints.JOBS_COUNT
  ? JobsCountResponse
  : T extends typeof PCEndpoints.PERSON_SEARCH
  ? PersonSearchResponse
  : T extends typeof PCEndpoints.COMPANY_SEARCH
  ? CompanySearchResponse
  : T extends typeof PCEndpoints.JOBS_SEARCH
  ? JobsSearchResponse
  : never;

export class ProxycurlAPI {
  cacheService: CacheService;
  config: Config;

  constructor() {
    // lookup configuration by html element id
    this.config = new Config();
    this.cacheService = new LocalStorageCacheService(this.config.CACHE_TTL);
  }

  async fetch<T extends PCEndpoints>(
    endpoint: T,
    cache_key: string | null,
    params: { [key: string]: string | number | boolean }
  ): Promise<ResponseFromEndpoint<T>> {
    return this.baseRequest(endpoint, 'get', params, {}, cache_key) as Promise<ResponseFromEndpoint<T>>;
  }

  async sendRequest(
    endpoint: string,
    method: 'get' | 'post' = 'get',
    params: { [key: string]: string | number | boolean } = {},
    payload: { [key: string]: string | number | boolean } = {}
  ) {
    return this.baseRequest(endpoint, method, params, payload) as object;
  }

  private async baseRequest<T extends PCEndpoints>(
    endpoint: T | string,
    method: string,
    params: { [key: string]: string | number | boolean } = {},
    payload: { [key: string]: string | number | boolean | object } = {},
    cache_key?: string | null
  ): Promise<ResponseFromEndpoint<T> | object> {
    const baseUrl = this.isPCEndpoint(endpoint) ? this.config.PX_API_BASE_URL : this.config.API_BASE_URL;

    const callParams = new URLSearchParams();
    for (const param in params) {
      if (!params[param] || params[param] === '') {
        continue;
      }
      callParams.append(param, String(params[param]));
    }

    let cacheKey: string | undefined = undefined;
    let cached: string | null = null;

    // caching only supported for PCEndpoints
    if (this.isPCEndpoint(endpoint)) {
      let freshMode = false; // just demo, disable fresh mode for now
      // only for person profile
      freshMode = endpoint === PCEndpoints.PERSON_PROFILE ? freshMode : false;
      /* TODO: Need to update the cache usage.
      * When the freshMode is true, it should be able to fallback
      * to the normal cache on the case of when the fresh credit is depleted.
      * The current implementation is not able to fallback to the normal cache.
      * The implementation should consider that the fallback doesn't happen
      * when the user just upgraded to a plan with fresh mode, the cache should
      * be cleared beforehand to prevent using normal cache when the fresh mode
      * is enabled.
      */
      if (cache_key) {
        cacheKey = this.cacheKey(endpoint, cache_key, freshMode);
        cached = this.cacheService.get(cacheKey);
      }
      callParams.append('_cached',`${cached ? 'True' : 'False'}`);

      // if the endpoint in DynamicCost then add new params
      // that will identify the count of result and used for cost counting
      if (endpoint in DynamicCostPCEndpoints  && cached !== null){
        const cached_json = JSON.parse(cached) as ResponseFromEndpoint<T>;
        const value = cached_json[
          DynamicCostPCEndpoints[endpoint] as keyof ResponseFromEndpoint<T>
        ];
        if (Array.isArray(value)) {
          callParams.append('_attribute_count', `${value.length}`);
        }
      }

      if ('_requested_attribute' in params && cached !== null) {
        let empty = false;
        const cached_json = JSON.parse(cached) as ResponseFromEndpoint<T>;
        const requested_attribute_params = params[
          '_requested_attribute'
        ] as string;
        const splitted_requested_attribute =
          requested_attribute_params.split(', ');
        let requested_attribute = null;
        let index_value = null;
        let index_attribute = null;
        if (splitted_requested_attribute.length === 1) {
          requested_attribute =
            splitted_requested_attribute[0] as keyof ResponseFromEndpoint<T>;
        } else if (splitted_requested_attribute.length === 2) {
          requested_attribute =
            splitted_requested_attribute[0] as keyof ResponseFromEndpoint<T>;
          index_attribute = splitted_requested_attribute[1];
        } else if (splitted_requested_attribute.length === 3) {
          requested_attribute =
            splitted_requested_attribute[0] as keyof ResponseFromEndpoint<T>;
          index_value = parseInt(splitted_requested_attribute[1]);
          index_attribute = splitted_requested_attribute[2];
        }

        if (
          requested_attribute !== null &&
          requested_attribute in cached_json
        ) {
          const attribute_value = cached_json[requested_attribute];
          if (attribute_value === null) {
            empty = true;
          } else if (index_value === null && index_attribute !== null) {
            // for requested_attribute length = 2
            const attribute_value_object = attribute_value as Record<
              string, unknown
            >;
            if (index_attribute in attribute_value_object) {
              const value = attribute_value_object[index_attribute];
              if (value === null) {
                empty = true;
              }
            }
          } else if (index_value !== null && attribute_value instanceof Array) {
            // for requested_attribute length = 3
            if (index_value >= 0 && index_value < attribute_value.length) {
              if (
                index_attribute !== null &&
                index_attribute in attribute_value[index_value]
              ) {
                // eslint-disable-next-line max-len
                // eslint-disable-next-line @typescript-eslint/no-unsafe-assignment, @typescript-eslint/no-unsafe-member-access
                const value = attribute_value[index_value][index_attribute];
                if (value === null) {
                  empty = true;
                }
              }
            }
          }
        }

        if (empty) {
          callParams.append('_empty', 'True');
        }
      }
    } else {
      callParams.append('_cached', 'False');
    }

    const url = `${baseUrl}${endpoint}?${callParams.toString()}`;
    const headers = {
      'Authorization': `Bearer ${this.config.API_KEY}`,
      'Content-Type': 'application/json',
    };

    if (this.config.PX_CLOUDFLARE_ACCESS_CLIENT_ID && this.config.PX_CLOUDFLARE_ACCESS_CLIENT_SECRET) {
      headers['CF-Access-Client-Id'] = this.config.PX_CLOUDFLARE_ACCESS_CLIENT_ID;
      headers['CF-Access-Client-Secret'] = this.config.PX_CLOUDFLARE_ACCESS_CLIENT_SECRET;
    }

    try {
        const response = await fetch(url, {
          method,
          headers,
          ...(method === 'post' ? { body: JSON.stringify(payload) } : {}),
        });

        if (cached) {
          return JSON.parse(cached) as ResponseFromEndpoint<T>;
        }  
        
        if (response.status === 200) {
          const freshMode = response.headers['x-sapiengraph-fresh-mode'] === 'True';
          const data = await response.json();
        
          if (cache_key && this.isPCEndpoint(endpoint)) {
              const newCacheKey = this.cacheKey(endpoint, cache_key, freshMode);
              this.cacheService.set(
                  newCacheKey,
                  JSON.stringify(data)
              );
          }

          return data as ResponseFromEndpoint<T>;
        } else {           
            const responseText = await response.text();
            
            if (response.status === 401) {
              console.log(`[401 Unauthorized] => ${responseText}`);
              throw new Error('Error to launch Sapiengraph. Please Enable Sapiengraph on this sheet by clicking Extension Menu -> Sapiengraph -> Launch Sapiengraph.');
            }
    
            if (response.status === 429) {
              throw new Error('You`ve hit the maximum of 300 formulas! Please wait a moment before adding more.');
            }
    
            if ([400, 403, 404, 503].includes(response.status || 0)) {
              const responseJSON = await response.json();
              throw new Error(responseJSON.description || responseJSON.message);
            }
    
            if (response.status && response.status >= 500) {
              console.log(`Error Response ${response.status} : ${responseText}`);
            }
    
            throw new Error("Internal server error. We're working on fixing it, please try again later.");
        }   

    } catch (error) {        
        throw error;
}
  }

  private cacheKey(
    endpoint: PCEndpoints,
    cache_key: string,
    freshMode = false
  ): string {
    return `${freshMode ? 'fresh-' : ''}${PCCacheKey[endpoint]}${cache_key}`;
  }

  private isPCEndpoint(
    endpoint: string | PCEndpoints
  ): endpoint is PCEndpoints {
    return Object.values(PCEndpoints).includes(endpoint as PCEndpoints);
  }
}

export default ProxycurlAPI;
