const enum SG_JOB_FORMULA_NAMES {
  SG_JOB = 'SG_JOB',
  SG_JOBS_COUNT = 'SG_JOBS_COUNT',
  SG_JOBS_SEARCH = 'SG_JOBS_SEARCH',
}

export async function SG_JOB_IMPL_(
  LINKEDIN_JOB_URL: string,
  JOB_ATTRIBUTE: string
) {
  check_all_params(
    false,
    ['LINKEDIN_JOB_URL', 'JOB_ATTRIBUTE'],
    {LINKEDIN_JOB_URL, JOB_ATTRIBUTE}
  );

  LINKEDIN_JOB_URL = handleStringParam(LINKEDIN_JOB_URL);
  validateJobLinkedinURL(LINKEDIN_JOB_URL);
  JOB_ATTRIBUTE = handleStringParam(JOB_ATTRIBUTE);
  type attributeAliasesType = {
    [key:string]: string;
  };
  const attributeAliases: attributeAliasesType = {
    location_country: 'location,country',
    location_region: 'location,region',
    location_city: 'location,city',
    location_postal_code: 'location,postal_code',
    location_latitude: 'location,latitude',
    location_longitude: 'location,longitude',
    location_street: 'location,street',
    company_name: 'company,name',
    company_url: 'company,url',
    company_logo: 'company,logo',
  };
  if (!isKeyOf(DummyJobProfileResponse, JOB_ATTRIBUTE) &&
  !(JOB_ATTRIBUTE in attributeAliases)) {
    throw new Error(`Invalid value for "${JOB_ATTRIBUTE}".`);
  }
  const proxycurlAPI = new ProxycurlAPI();
  const response = await proxycurlAPI.fetch(
    PCEndpoints.JOB,
    LINKEDIN_JOB_URL,
    {
      "url": LINKEDIN_JOB_URL,
      "function_name": "SG_JOB",
    }
  );
  if (JOB_ATTRIBUTE in response || JOB_ATTRIBUTE in attributeAliases) {
    if (JOB_ATTRIBUTE in attributeAliases) {
      const [attribute1, attribute2]  = attributeAliases[JOB_ATTRIBUTE].split(',');
      if (attribute1 == 'location'){
        const result = response[
          attribute1 as keyof JobProfileResponse
        ]as JobLocation;
        if (result) {
          return handleResult(
            result[attribute2 as keyof JobLocation]
          );
        } else {
          throw new Error(`"${JOB_ATTRIBUTE}" property does not exist on Job`);
        }
      } else if (attribute1 == 'company'){
        const result = response[
          attribute1 as keyof JobProfileResponse
        ]as JobCompany;
        if (result) {
          return handleResult(
            result[attribute2 as keyof JobCompany]
          );
        } else {
          throw new Error(`"${JOB_ATTRIBUTE}" property does not exist on Job`);
        }
      }
    }
    return handleResult(
      response[JOB_ATTRIBUTE as keyof JobProfileResponse]
    );
  } else {
    throw new Error(`"${JOB_ATTRIBUTE}" property does not exist on Job`);
  }
}


export async function SG_JOBS_COUNT_IMPL_(
  LINKEDIN_PROFILE_URL: string,
  KEYWORD: string,
  GEO_ID: string,
  FLEXIBILITY: string,
  WHEN: string,
  EXPERIENCE_LEVEL: string,
  JOB_TYPE: string,
) {
  check_all_params(
    true,
    ['LINKEDIN_PROFILE_URL'],
    {
      LINKEDIN_PROFILE_URL,
      KEYWORD,
      GEO_ID,
      FLEXIBILITY,
      WHEN,
      EXPERIENCE_LEVEL,
      JOB_TYPE,
    }
  );

  LINKEDIN_PROFILE_URL = handleStringParam(LINKEDIN_PROFILE_URL);
  validateCompanyLinkedinURL(LINKEDIN_PROFILE_URL);
  KEYWORD = handleStringParam(KEYWORD, '');
  GEO_ID = handleStringParam(GEO_ID, '');
  FLEXIBILITY = handleStringParam(FLEXIBILITY, 'anything');
  const acceptedFlexibilityValues = ['remote', 'on-site', 'hybrid', 'anything'];
  if (!acceptedFlexibilityValues.includes(FLEXIBILITY)) {
    throw new Error('Invalid STATUS. Accepted values: anything [default], remote, on-site, hybrid');
  }
  WHEN = handleStringParam(WHEN, 'anytime');
  const acceptedWhenyValues = ['yesterday', 'past-week', 'past-month', 'anytime'];
  if (!acceptedWhenyValues.includes(WHEN)) {
    throw new Error('Invalid STATUS. Accepted values: anytime [default], yesterday, past-week, past-month');
  }
  EXPERIENCE_LEVEL = handleStringParam(EXPERIENCE_LEVEL, 'anything');
  const acceptedExperienceValues = ['internship', 'entry_level', 'associate', 'mid_senior_level', 'director', 'anything'];
  if (!acceptedExperienceValues.includes(EXPERIENCE_LEVEL)) {
    throw new Error('Invalid STATUS. Accepted values: anything [default], internship, entry_level, associate, mid_senior_level, director');
  }
  JOB_TYPE = handleStringParam(JOB_TYPE, 'anything');
  const acceptedJobValues = ['full-time', 'part-time', 'contract', 'internship', 'temporary', 'volunteer', 'anything'];
  if (!acceptedJobValues.includes(JOB_TYPE)) {
    throw new Error('Invalid STATUS. Accepted values: anything [default], full-time, part-time, contract, internship, temporary, volunteer');
  }
  const proxycurlAPI = new ProxycurlAPI();
  const company_response = await proxycurlAPI.fetch(
    PCEndpoints.COMPANY_PROFILE,
    LINKEDIN_PROFILE_URL,
    {
      "url": LINKEDIN_PROFILE_URL,
      "funding_data": "include", // to make cache same like other endpoint
      "function_name": "EMPTY_FUNCTION", // to waive the charge
    }
  );
  const search_id = company_response['search_id'] ? company_response['search_id'] : null;

  const response = new ProxycurlAPI().fetch(
    PCEndpoints.JOBS_COUNT,
    combine_cache_key(
      JOB_TYPE,
      EXPERIENCE_LEVEL,
      WHEN,
      FLEXIBILITY,
      GEO_ID,
      search_id ? search_id as unknown as string : 'null'
      ),
    {
      "job_type": JOB_TYPE,
      "experience_level": EXPERIENCE_LEVEL,
      "when": WHEN,
      "flexibility": FLEXIBILITY,
      "geo_id": GEO_ID,
      "keyword": KEYWORD,
      "search_id": search_id ? search_id : '',
      "function_name": "SG_JOBS_COUNT",
    }
  );
  return handleResult(response["count"]);
}


export async function SG_JOBS_SEARCH_IMPL_(
  LINKEDIN_PROFILE_URL: string,
  MAX_RESULT: number,
  KEYWORD: string,
  GEO_ID: string,
  FLEXIBILITY: string,
  WHEN: string,
  EXPERIENCE_LEVEL: string,
  JOB_TYPE: string,
) {
  check_all_params(
    true,
    ['LINKEDIN_PROFILE_URL'],
    {
      LINKEDIN_PROFILE_URL,
      MAX_RESULT,
      KEYWORD,
      GEO_ID,
      FLEXIBILITY,
      WHEN,
      EXPERIENCE_LEVEL,
      JOB_TYPE,
    }
  );

  LINKEDIN_PROFILE_URL = handleStringParam(LINKEDIN_PROFILE_URL);
  validateCompanyLinkedinURL(LINKEDIN_PROFILE_URL);
  MAX_RESULT = handleNumberParam(MAX_RESULT, 10);
  KEYWORD = handleStringParam(KEYWORD, '');
  GEO_ID = handleStringParam(GEO_ID, '');
  FLEXIBILITY = handleStringParam(FLEXIBILITY, 'anything');
  const acceptedFlexibilityValues = ['remote', 'on-site', 'hybrid', 'anything'];
  if (!acceptedFlexibilityValues.includes(FLEXIBILITY)) {
    throw new Error('Invalid STATUS. Accepted values: anything [default], remote, on-site, hybrid');
  }
  WHEN = handleStringParam(WHEN, 'anytime');
  const acceptedWhenyValues = ['yesterday', 'past-week', 'past-month', 'anytime'];
  if (!acceptedWhenyValues.includes(WHEN)) {
    throw new Error('Invalid STATUS. Accepted values: anytime [default], yesterday, past-week, past-month');
  }
  EXPERIENCE_LEVEL = handleStringParam(EXPERIENCE_LEVEL, 'anything');
  const acceptedExperienceValues = ['internship', 'entry_level', 'associate', 'mid_senior_level', 'director', 'anything'];
  if (!acceptedExperienceValues.includes(EXPERIENCE_LEVEL)) {
    throw new Error('Invalid STATUS. Accepted values: anything [default], internship, entry_level, associate, mid_senior_level, director');
  }
  JOB_TYPE = handleStringParam(JOB_TYPE, 'anything');
  const acceptedJobValues = ['full-time', 'part-time', 'contract', 'internship', 'temporary', 'volunteer', 'anything'];
  if (!acceptedJobValues.includes(JOB_TYPE)) {
    throw new Error('Invalid STATUS. Accepted values: anything [default], full-time, part-time, contract, internship, temporary, volunteer');
  }

  const company_response = new ProxycurlAPI().fetch(
    PCEndpoints.COMPANY_PROFILE,
    LINKEDIN_PROFILE_URL,
    {
      "url": LINKEDIN_PROFILE_URL,
      "funding_data": "include", // to make cache same like other endpoint
      "function_name": "EMPTY_FUNCTION", // to waive the charge
    }
  );

  const search_id = company_response['search_id'] ? company_response['search_id'] : null;

  const proxycurlAPI = new ProxycurlAPI();
  const response = await proxycurlAPI.fetch(
    PCEndpoints.JOBS_SEARCH,
    null,
    {
      "search_id": search_id ? search_id : '',
      "max_result": MAX_RESULT,
      "keyword": KEYWORD,
      "geo_id": GEO_ID,
      "flexibility": FLEXIBILITY,
      "when": WHEN,
      "experience_level": EXPERIENCE_LEVEL,
      "job_type": JOB_TYPE,
      "function_name": "SG_JOBS_SEARCH",
    }
  );

  const results: string[] = [];
  response["job"].forEach((result) => {
    results.push(result.job_url);
  });

  return handleResult(results);
}

async function wrap_SG_JOB_IMPL_(LINKEDIN_JOB_URL: string, JOB_ATTRIBUTE: string) {
  return new Promise(async(resolve: (value: typeof BaseValueObject) => void) => {
    try {
      const resp = await SG_JOB_IMPL_(LINKEDIN_JOB_URL, JOB_ATTRIBUTE);
      resolve(StringValueObject.create(resp));
    } catch (error) {
      resolve(StringValueObject.create("Failed to fetch job data"));
    }
  });
}

async function wrap_SG_JOBS_COUNT_IMPL_(LINKEDIN_PROFILE_URL: string, KEYWORD: string, GEO_ID: string, FLEXIBILITY: string, WHEN: string, EXPERIENCE_LEVEL: string, JOB_TYPE: string) {
  return new Promise(async(resolve: (value: typeof BaseValueObject) => void) => {
    try {
      const resp = await SG_JOBS_COUNT_IMPL_(LINKEDIN_PROFILE_URL, KEYWORD, GEO_ID, FLEXIBILITY, WHEN, EXPERIENCE_LEVEL, JOB_TYPE);
      resolve(StringValueObject.create(resp));
    } catch (error) {
      console.error('Error fetching job count', error);
      resolve(StringValueObject.create("Failed to fetch job count"));
    }
  });
}

async function wrap_SG_JOBS_SEARCH_IMPL_(LINKEDIN_PROFILE_URL: string, MAX_RESULT: number, KEYWORD: string, GEO_ID: number, FLEXIBILITY: string, WHEN: string, EXPERIENCE_LEVEL: string, JOB_TYPE: string) {
  return new Promise(async(resolve: (value: typeof BaseValueObject) => void) => {
    try {
      const resp = await SG_JOBS_SEARCH_IMPL_(LINKEDIN_PROFILE_URL, MAX_RESULT, KEYWORD, GEO_ID, FLEXIBILITY, WHEN, EXPERIENCE_LEVEL, JOB_TYPE);
      if (Array.isArray(resp)) {
        resolve(ArrayValueObject.createByArray(resp.map(i => [i])));
      } else {
        resolve(StringValueObject.create(resp));
      }
    } catch (error) {
      console.error('Error fetching job search', error);
      resolve(StringValueObject.create("Failed to fetch job search"));
    }
  });
}

class SG_JOB extends AsyncBaseFunction {
  calculate(...variants) {
    this.showLoading();
    if (variants.length < 2) {
      return StringValueObject.create('Error: Invalid number of parameters');
    }

    const LINKEDIN_JOB_URL = stringFromVariant(variants[0]);
    const JOB_ATTRIBUTE = stringFromVariant(variants[1]);

    return new AsyncObject(wrap_SG_JOB_IMPL_(LINKEDIN_JOB_URL, JOB_ATTRIBUTE));
  }
}

class SG_JOBS_COUNT extends AsyncBaseFunction {
  calculate(...variants) {
    this.showLoading();

    if (variants.length < 1) {
      return StringValueObject.create('Error: Invalid number of parameters');
    }

    const LINKEDIN_PROFILE_URL = stringFromVariant(variants[0]);
    let KEYWORD = '';
    if (variants.length > 1) {
      KEYWORD = stringFromVariant(variants[1]);
    }

    let GEO_ID = '';
    if (variants.length > 2) {
      GEO_ID = stringFromVariant(variants[2]);
    }

    let FLEXIBILITY = '';
    if (variants.length > 3) {
      FLEXIBILITY = stringFromVariant(variants[3]);
    }

    let WHEN = '';
    if (variants.length > 4) {
      WHEN = stringFromVariant(variants[4]);
    }

    let EXPERIENCE_LEVEL = '';
    if (variants.length > 5) {
      EXPERIENCE_LEVEL = stringFromVariant(variants[5]);
    }

    let JOB_TYPE = '';
    if (variants.length > 6) {
      JOB_TYPE = stringFromVariant(variants[6]);
    }

    return new AsyncObject(wrap_SG_JOBS_COUNT_IMPL_(
      LINKEDIN_PROFILE_URL,
      KEYWORD,
      GEO_ID,
      FLEXIBILITY,
      WHEN,
      EXPERIENCE_LEVEL,
      JOB_TYPE
    ));
  }
}

class SG_JOB_SEARCH extends AsyncBaseFunction {
  calculate(...variants) {
    this.showLoading();

    if (variants.length < 1) {
      return StringValueObject.create('Error: Invalid number of parameters');
    }

    const LINKEDIN_PROFILE_URL = stringFromVariant(variants[0]);

    let MAX_RESULT = 10;
    if (variants.length > 1) {
      MAX_RESULT = handleNumberParam(variants[1], 10);
    }

    let KEYWORD = '';
    if (variants.length > 2) {
      KEYWORD = stringFromVariant(variants[2]);
    }

    let GEO_ID = 0;
    if (variants.length > 3) {
      GEO_ID = intFromVariant(variants[3]);
    }

    let FLEXIBILITY = '';
    if (variants.length > 4) {
      FLEXIBILITY = stringFromVariant(variants[4]);
    }

    let WHEN = '';
    if (variants.length > 5) {
      WHEN = stringFromVariant(variants[5]);
    }

    let EXPERIENCE_LEVEL = '';
    if (variants.length > 6) {
      EXPERIENCE_LEVEL = stringFromVariant(variants[6]);
    }

    let JOB_TYPE = '';
    if (variants.length > 7) {
      JOB_TYPE = stringFromVariant(variants[7]);
    }

    return new AsyncObject(wrap_SG_JOBS_SEARCH_IMPL_(
      LINKEDIN_PROFILE_URL,
      MAX_RESULT,
      KEYWORD,
      GEO_ID,
      FLEXIBILITY,
      WHEN,
      EXPERIENCE_LEVEL,
      JOB_TYPE
    ));
  }
}

